i {
  padding: .1em;
  padding-bottom: -1em;
  font-size: 1em;
}

.under-construction {
  text-align: center;
  background-color: #3a4a4b;
  color: rgb(241, 23, 23);
}

.hero-container {
    background: url('../images/banner_pic.jpg') center center/cover no-repeat;
    position: relative;
    height: 10em;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2); */
    object-fit: contain;
    text-align: center;
  }
  
  .banner_img {
    z-index: 0;
    width: 100%;
    padding-top: 300px;
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size:contain;
    position: relative;
    border: 100px black;
  }
  
  .hero-container > h3 {
    position: absolute;
    margin-top: -60px;
    color: #fff;
    font-size: 45px;
  }
  
  .hero-container > p {
    position: absolute;
    margin-top: 60px;
    color: #fff;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  }
  
  .hero-btns {
    margin-top: 32px;
  }
  
  .hero-btns .btn {
    margin: 6px;
  }
  
  .fa-play-circle {
    margin-left: 4px;
  }
  
  @media screen and (max-width: 960px) {
    .under-construction {
font-size: .8rem;
    }

    .hero-container {
      display: none;
    }
  
    .hero-container > h1 {
      font-size: 70px;
      margin-top: -150px;
    }
  }
  
  @media screen and (max-width: 768px) {
    .hero-container > h1 {
      font-size: 50px;
      margin-top: -100px;
    }
  
    .hero-container > p {
      font-size: 30px;
    }
  
    .btn-mobile {
      display: block;
      text-decoration: none;
    }
  
    .btn {
      width: 100%;
    }
  }